import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-heading',
  styleUrl: "orderHeading.css"
})
export class OrderHeading {
    @Prop() heading: string;
    @Prop() link: string;

    render() {
        return <Host>
            <h2>{this.heading}</h2>
            {this.link && <ks-button link={this.link} transparent round icon="edit" ariaLabel="Edytuj dane" />}
        </Host>;
    }
}

